













import Vue from 'vue';
import { defineComponent, onMounted, onUnmounted } from '@vue/composition-api';
import VueConfetti from 'vue-confetti';
import {
  BButton,
} from 'bootstrap-vue';

Vue.use(VueConfetti);

export default defineComponent({
  name: 'Winner',

  components: {
    BButton,
  },

  props: {
    winnerName: {
      type: String,
      required: true,
    },
  },

  setup(props, { emit, root }) {
    const startNewGame = () => {
      emit('new-game');
    };

    onMounted(() => {
      root.$confetti.start();
    });

    onUnmounted(() => {
      root.$confetti.stop();
    });

    return {
      startNewGame,
    };
  },
});
