






















import { defineComponent, ref } from '@vue/composition-api';
import {
  BFormTags, BButton, BForm,
} from 'bootstrap-vue';
import startMembers from './startMembers';

export default defineComponent({
  name: 'TargetsCreator',

  components: {
    BFormTags,
    BButton,
    BForm,
  },

  setup(props, { emit }) {
    const targets = ref(startMembers);

    const addTargets = () => {
      emit('input', targets.value);
    };

    return {
      targets,
      addTargets,
    };
  },
});
